.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: rgb(38, 38, 38);
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.nav-pills>a:hover {
  background-color: #4d5057;
  color: #d6d6d6;
  border-radius: 0%
}

.nav-pills>a.nav-link.active {
  background-color: #4d5057;
  color: white;
  border-radius: 0%
}

.nav-pills>a {
  color: #b1b1b1;
}

.nav-pills {
  background-color: #37383c;
  color: white;
  width:200px;
  height: 100%;
}

.logoSide{
  align-items: center;
  justify-content: center;
}
.map{
  margin: 10px;
  height: 300px;
}

.stats{
  background-color: #545454;
  margin: 10px;
  margin-left: 20px;
  padding: 10px;
  height: 100%;
}

.App-link {
  color: #61dafb;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
